import React from "react";

const sizeClasses = {
  txt1456GutenbergBold40: "font-bold font-gutenberg",
  txtInterBold18Deeporange700: "font-bold font-inter",
  txtInterBold18: "font-bold font-inter",
  txtInterRegular1735: "font-inter font-normal",
  txtInterSemiBold3615: "font-inter font-semibold",
  txtInterBold16: "font-bold font-inter",
  txtIstokWebItalic3513: "font-istokweb font-normal italic",
  txtInterSemiBold2313: "font-inter font-semibold",
  txtInterRegular18: "font-inter font-normal",
  txtInterBold18PinkA200: "font-bold font-inter",
} as const;

export type TextProps = Partial<{
  className: string;
  size: keyof typeof sizeClasses;
  as: any;
}> &
  React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLSpanElement>,
    HTMLSpanElement
  >;

const Text: React.FC<React.PropsWithChildren<TextProps>> = ({
  children,
  className = "",
  size,
  as,
  ...restProps
}) => {
  const Component = as || "p";

  return (
    <Component
      className={`text-left ${className} ${size && sizeClasses[size]}`}
      {...restProps}
    >
      {children}
    </Component>
  );
};

export { Text };
